section.DocPage {
    height: 100%;
    width: 100%;

    h1 {
        font-family: 'Raleway', sans-serif;
        margin: 40px 0px;
    }
    h5 {
        line-height: 1.5em;
        font-weight: 300;
    }

    .full-width-height {
        height: 100%;
        width: 100%;
    }
    .nav-path {
        padding-bottom: 20px;
        padding-left: 5px;
        margin-top: 20px;
        font-size: .8em;
        font-family: 'Roboto Mono', monospace;
        border-bottom: 3px solid #41228E;
    }
    .standard-description {
        margin-bottom: 60px;
    }
    .nav-item {
        a {
            color: #41228E;
        }
    }

}